import React from 'react';
import '../../css/footer.css';
import { img } from '../../functions/img.js';

const Footer = () => (
    <footer>
        <div>
            <div className="footer-app">
                <h2>?</h2>
                <button className="footer-button" data-toggle="modal" data-target=".bs-example-modal-sm">
                    !
            </button>
            </div>
        </div>

        <div className="modal fade bs-example-modal-sm" tabIndex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div className="modal-dialog modal-sm" role="document">
                <div className="modal-content text-center">
                    <h4 className="modal-title">APP</h4>
                    <img src={img("/img/app.png")} alt="app download" />
                </div>
            </div>
        </div>
        <div className="container">
            <div className="container footer-link">
                <div className="row">
                    <div className="col-md-4">
                        <p></p>
                        <a href="http://news.sina.com.cn/"></a>
                        <a href="http://news.qq.com/"></a>
                        <a href="http://news.163.com/"></a>
                    </div>
                    <div className="col-md-4">
                        <p></p>
                        <a>&copy;2016 </a>
                    </div>
                    <div className="col-md-4">
                        <p></p>
                        <a>antinetfraud@163.com</a>
                    </div>
                </div>
            </div>
        </div>
    </footer>
)
export { Footer }



